        { g: 247, l: 243, tmp: 645 },
        { g: 990, l: 93, tmp: 844 },
        { g: 757, l: 273, tmp: 81 },
        { g: 396, l: 387, tmp: 15 },
        { g: 518, l: 425, tmp: 268 },
        { g: 246, l: 48, tmp: 423 },
        { g: 841, l: 426, tmp: 530 },
        { g: 844, l: 69, tmp: 571 },
        { g: 818, l: 31, tmp: 27 },
        { g: 248, l: 180, tmp: 168 },
        { g: 251, l: 185, tmp: 462 },
        { g: 727, l: 57, tmp: 15 },
        { g: 636, l: 175, tmp: 7 },
        { g: 310, l: 127, tmp: 494 },
        { g: 380, l: 442, tmp: 609 },
        { g: 923, l: 192, tmp: 43 },
        { g: 161, l: 487, tmp: 817 },
        { g: 596, l: 320, tmp: 718 }
      ]
    }
  },
  mounted() {
    // 在 mounted 中构建，防止created 中无法获取标签元素
    const data = this.data
    const heatmapPlot = new Heatmap(document.getElementById('container'), {
      data,
      type: 'density',
      xField: 'g',
      yField: 'l',
      colorField: 'tmp',
      color: '#F51D27-#FA541C-#FF8C12-#FFC838-#FAFFA8-#80FF73-#12CCCC-#1890FF-#6E32C2',
      legend: {
        position: 'bottom'
      },
      annotations: [
        {
          type: 'image',
          start: ['min', 'max'],
          end: ['max', 'min'],
          src: 'https://gw.alipayobjects.com/zos/rmsportal/NeUTMwKtPcPxIFNTWZOZ.png'
        }
      ]
    })
    heatmapPlot.render()
  },
  methods: {},
  created() {
    // var wrapperElement = '<div style="position:relative;"></div>'
    // console.log(document.getElementById('container'))
    // document.getElementById('container').appendChild(wrapperElement)
    // setTimeout(() => {
    // }, 2000)
  }
}
</script>

<style lang="stylus" scoped>
.demo {
  display: flex;
  flex-flow: wrap;

  .box {
    box-shadow: 0px 2px 25px 0px rgba(133, 153, 181, 0.2);
    border-radius: 10px;
    display: inline-block;
    padding: 0 20px 20px;
    margin: 20px;

    h3 {
      line-height: 50px;
      height: 50px;
      text-align: center;
    }
  }

  #container {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGs {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGz {
    display: block;
    width: 800px;
    height: 400px;
  }

  #containerpt {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerGet {
    display: block;
    width: 500px;
    height: 300px;
  }

  #containerTexture {
    display: block;
    width: 500px;
    height: 300px;
  }
}
</style>



